import conf from './conf'
import axios from 'axios'
import { Toast } from 'mint-ui';

var oproto = Object.prototype;
var serialize = oproto.toString;
var Rxports = {
  /**
   * axiosjq ajax
   * @param {String} type			post
   * @param {String} url				
   * @param {String} time			
   * @param {Object} data			
   * @param {String} dataType		xml html json ...
   * @param {Object} headers			headers
   * @param {Function} success		,[data, res]
   * @param {Function} error		
   * @param return 
   */
  ajax: function(opt) {

    var opts = opt || {};

    if (!opts.url) {
      alert('');
      return false;
    }

    axios({
      method: opts.type || 'POST',
      url: opts.url,
      params: opts.data || {},
      header: opts.header || {
        //'Content-Type':'application/x-www-form-urlencoded'
        'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
      },
      // `baseURL`  `url`  `url`  URL
      //  `baseURL`  axios  URL
      //baseURL:'http://member.yifriend.net/',
      baseURL: memberAPI.activeAPI.homeUrl,

      // baseURL:'http://113.106.202.53:13887',
      timeout: opts.time || 10 * 10000,
      responseType: opts.dataType || 'json'
    }).then(function(res) {
      var url = window.location.href,
        busId = opts.data.json.busId,
        tuijianCode;
      url = url.split(busId);
      if (res.status == 200) {
        // console.log('111111',memberAPI.activeAPI.goURL+memberAPI.activeAPI.goMen+'#/pickclub/'+opts.data.json.busId+url[1])
        // console.log('common.js',res.data)
        if (res.data.code == 0) {
          if (opts.success) {
            opts.success(res.data, res);
          }
        }
        if (res.data.code == 1) {
          if (opts.error) {
            opts.error(res.data, res);
          }
        }
        if (res.data.code > 1) {

          switch (res.data.code) {
            case 4004:
              opts.warning(res.data, res);
              break;
            case 4006:
              // window.location.href=res.data.data;
              window.location.href = res.data.url;
              break;
            case 5017:
              window.location.href = res.data.msg;
              break;
            case 5025:
              if (url[1] != undefined) {
                window.location.href = memberAPI.activeAPI.goURL + memberAPI.activeAPI.goMen + '#/pickclub/' + opts.data.json.busId + url[1];
                // window.location.href= memberAPI.activeAPI.goURL+'/#/pickclub/'+opts.data.json.busId;
              } else {
                window.location.href = memberAPI.activeAPI.goURL + memberAPI.activeAPI.goMen + '#/pickclub/' + opts.data.json.busId;
                // window.location.href= memberAPI.activeAPI.goURL+'/html/phone/index.html#/pickclub/'+opts.data.json.busId;
                // window.location.href= memberAPI.activeAPI.goURL+'/#/pickclub/'+opts.data.json.busId;
              }
              break;
            default:
              opts.warning(res.data, res);
          }
        }
      } else {
        if (data.error) {
          opts.error(error);
        } else {
          alert('[timeout]');
        }

      }
    }).catch(function(error) {
      console.log(error);
      if (opts.error) {
        opts.error(error);
      } else {
        console.log("[timeout]")
      }
    });

  },
  tipsMsg(msg,time){
	var _this=this;
	var time=time||1500
	Toast({
    	message:msg,
    	duration:time
    });
},
  //
  formatTime: function(date, val) {
    var date = new Date(date);
    var year = date.getFullYear()
    var month = date.getMonth() + 1
    var day = date.getDate()

    var hour = date.getHours()
    var minute = date.getMinutes()
    var second = date.getSeconds()

    if (val) {
      return [year, month, day].map(this.formatNumber).join('-');
    } else {
      return [year, month, day].map(this.formatNumber).join('-') + ' ' + [hour, minute, second].map(this.formatNumber).join(':');
    }
  },
  //
  checkPhone: function(phone) {
    if (!(/^1[34578]\d{9}$/.test(phone))) {
      return true;
    } else {
      return false;
    }
  },
  //
  checkNumber: function(number) {
    if (!(/^\+?[1-9][0-9]*$/.test(number))) {
      return true;
    } else {
      return false;
    }
  },
  // 
  checkEmail: function(email) {
    if (!(/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/.test(email))) {
      return true;
    } else {
      return false;
    }
  },
  //
  checkidCard(card) {
    var code = card.toUpperCase();
    var city = {
      11: "",
      12: "",
      13: "",
      14: "",
      15: "",
      21: "",
      22: "",
      23: " ",
      31: "",
      32: "",
      33: "",
      34: "",
      35: "",
      36: "",
      37: "",
      41: "",
      42: " ",
      43: "",
      44: "",
      45: "",
      46: "",
      50: "",
      51: "",
      52: "",
      53: "",
      54: " ",
      61: "",
      62: "",
      63: "",
      64: "",
      65: "",
      71: "",
      81: "",
      82: "",
      91: " "
    };
    var tip = "";
    var pass = true;

    if (!code ||
      !/^[1-9][0-9]{5}(19[0-9]{2}|200[0-9]|2010)(0[1-9]|1[0-2])(0[1-9]|[12][0-9]|3[01])[0-9]{3}[0-9xX]$/i
      .test(code)) {
      tip = "";
      pass = false;
    } else if (!city[code.substr(0, 2)]) {
      tip = "";
      pass = false;
    } else {
      //18
      if (code.length == 18) {
        code = code.split('');
        //(aiWi)(mod 11)
        //
        var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8,
          4, 2
        ];
        //
        var parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
        var sum = 0;
        var ai = 0;
        var wi = 0;
        for (var i = 0; i < 17; i++) {
          ai = code[i];
          wi = factor[i];
          sum += ai * wi;
        }
        var last = parity[sum % 11];
        if (parity[sum % 11] != code[17]) {
          //tip = "";
          pass = false;
        }
      }
    }
    if (!pass) {
      return false;
    } else {
      return true;
    }
  },
  //
  checkName: function(name) {
    if (!(/^([\u4e00-\u9fa5]{1,20}|[a-zA-Z\.\s]{1,20})$/.test(name))) {
      return true;
    } else {
      return false;
    }
  },
  //0
  formatNumber: function(n) {
    n = n.toString()
    return n[1] ? n : '0' + n
  },
  // 
  checkPassWord(password) {
    if (!(/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,15}$/.test(password))) {
      return true;
    } else {
      return false;
    }
  },
  /*argumentslengthJS*/
  isArrayLike: function(obj) {
    if (!obj)
      return false
    var n = obj.length
    if (n === (n >>> 0)) { //length
      var type = serialize.call(obj).slice(8, -1)
      if (/(?:regexp|string|function|window|global)$/i.test(type))
        return false
      if (type === "Array")
        return true
      try {
        if ({}.propertyIsEnumerable.call(obj, "length") === false) { //
          return /^\s?function/.test(obj.item || obj.callee)
        }
        return true
      } catch (e) { //IENodeList
        return !obj.window //IE6-8 window
      }
    }
    return false
  },
  /*,,*/
  each: function(obj, fn) {
    var That = this;
    if (obj) { //null, undefined
      var i = 0
      if (That.isArrayLike(obj)) {
        for (var n = obj.length; i < n; i++) {
          if (fn(i, obj[i]) === false)
            break
        }
      } else {
        for (i in obj) {
          if (obj.hasOwnProperty(i) && fn(i, obj[i]) === false) {
            break
          }
        }
      }
    }
  },
  /**
   * url
   * @param name 	
   * @param Url 		
   * @param return
   */
  getUrlQuery: function(name, Url) {

    //URL GET 
    
    var reg = new RegExp("(^|\\?|&)" + name + "=([^&]*)(\\s|&|$)", "i"),
      url = Url || location.href;
    if (reg.test(url)) return unescape(RegExp.$2.replace(/\+/g, " "));
    return "";

  },

  /**
   * 
   * @param src
   * @returns {{}}
   */
  copy(obj) {
    var dst = obj.constructor === Array ? [] : {};
    for (var prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        if (typeof obj[prop] == 'object') {
          dst[prop] = this.copy(obj[prop])
        } else {
          dst[prop] = obj[prop];
        }
      }
    }
    return dst;
  }


};
export default Rxports;



